@import url(https://fonts.googleapis.com/css?family=Ovo);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1.5vh 1.5vw 0vh;
}

.logo {
  max-height: 8.5vh;
}

.releases-table {
  display: flex;
  flex-wrap: wrap;
  max-width: 700px;
  margin: 0 auto 5vh;
}

.releases-table-cell {
  background: black;
  overflow: hidden;
  box-sizing: border-box;
  font-size: 20px;
  margin-top: 5px;
}

.releases-table-cell:nth-child(2n - 1) {
  border-radius: 8px 0 0 8px;
}

.releases-table-cell:nth-child(2n) {
  border-radius: 0 8px 8px 0;
}

.releases-table-cell-title {
  width: 80%;
}

.releases-table-cell-title button {
  padding: 2.5vh 3vw;
  color: white;
  background: black;
  width: 100%;
  height: 100%;
  border: none;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
}

.releases-table-cell-year {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
}

.release-mini-image {
  width: 50%;
  border-radius: 2px;
  margin: 1vh 0;
}

.releases-table-cell-year a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Release-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  max-width: 80%;
  margin: 0 auto;
  padding: 2vh 3vh 0 3vh;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.Release-image {
  max-width: 300px;
  border: 10px solid black;
  border-radius: 10px;
}

.Release-platform-links-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.Release-platform-icon {
  max-width: 7vh;
  padding: 1vh;
}

.Release-embed-container iframe {
  border: 1;
  box-sizing: border-box;
  border-radius: 3px;
  margin-top: 1vh;
  width: 100%;
  height: 80px;
}

.Release-fundraiser-embed-container {
  position: relative;
}

.Release-fundraiser-embed-container iframe {
  border: 4px solid black;
  border-radius: 10px;
}

.Release-fundraiser-logo {
  position: absolute;
  right: 30px;
  bottom: 17px;
  width: 70px;
}

.platforms-links-container {
  background-color: rgba(54, 102, 147,0.5);
  position: absolute;
  width: 100%;
  bottom: 0;
}

.platforms-links-ul {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  right: 0;
  list-style: none;
  margin: 0 auto;
  text-align: center;
  padding: 0 7.4vw;
}

.platform-link-li {
  margin: 2px 5px;
}

.platform-link-li a img {
  max-width: 6.5vmax;
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
}

.action-buttons-container {
  position: relative;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  min-width: 50vw;
}

.action-button {
  cursor: pointer;
  width: 60vw;
  color: rgba(230, 213, 235);
  margin: 1vh;
  padding: 1.2vw 4vw;
  font-family: inherit;
  font-size: 5vh;
  border-radius: 13px;
  background-color: rgba(210, 0, 20, 0.9);
}

html, body {
  margin: 0;
  font-family: Ovo, Georgia, sans-serif;
  background: #032546;
}

ul {
  padding: 0;
}

.App {
  height: 100vh;
  width: 100vw;
  background-image: url(http://quitetheidea.com/static/media/guitar_cropped_xparent_bkgd_dark50.464562bb.png);
  background-position-x: left;
  background-position-y: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: absolute;
  color: white;
}

